import Vue from 'vue'
import VeeValidate, { Validator } from 'vee-validate'

Validator.extend('nickName', { //用户名验证 4-14位
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^[A-z0-9_-]{4,16}$/.test(val)
    }
})

Validator.extend('password', { //密码强度验证 最少6位, 包括一个大写一个小写字母, 一个数字, 一个特殊字符
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/.test(val)
    }
})

Validator.extend('integer', { //整数
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^-?\d+$/.test(val)
    }
})

Validator.extend('plusinteger', { //正整数
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^\d+$/.test(val)
    }
})

Validator.extend('negativeinteger', { //负整数
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^-\d+$/.test(val)
    }
})

Validator.extend('number', { //数字
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^-?\d*\.?\d+$/.test(val)
    }
})

Validator.extend('plusnumber', { //正数
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^\d*\.?\d+$/.test(val)
    }
})

Validator.extend('negativenumber', { //负数
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^-\d*\.?\d+$/.test(val)
    }
})

Validator.extend('emailLocal', { //邮箱
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/.test(val)
    }
})

Validator.extend('phone', { //手机号码
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^1[34578]\d{9}$/.test(val)
    }
})

Validator.extend('idcard', { //身份证号码
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/.test(val)
    }
})

Validator.extend('urlLocal', { //url
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^((https?|ftp|file):\/\/)?([\da-z\.-]+)\.([a-z\.]{2,6})([\/\w \.-]*)*\/?$/.test(val)
    }
})

Validator.extend('ipv4', { //IPv4地址  115.28.47.26
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(val)
    }
})

Validator.extend('rgb16', { //16进制颜色  #b8b8b8
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^#?([a-fA-F0-9]{6}|[a-fA-F0-9]{3})$/.test(val)
    }
})

Validator.extend('easydate', { //日期 (简单匹配)
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^\d{4}(\-)\d{1,2}\1\d{1,2}$/.test(val)
    }
})

Validator.extend('complexdate', { //日期 (复杂匹配)
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^(?:(?!0000)[0-9]{4}-(?:(?:0[1-9]|1[0-2])-(?:0[1-9]|1[0-9]|2[0-8])|(?:0[13-9]|1[0-2])-(?:29|30)|(?:0[13578]|1[02])-31)|(?:[0-9]{2}(?:0[48]|[2468][048]|[13579][26])|(?:0[48]|[2468][048]|[13579][26])00)-02-29)$/.test(val)
    }
})

Validator.extend('qqcard', { //qq号码 5-11位
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^[1-9][0-9]{4,10}$/.test(val)
    }
})

Validator.extend('wxcard', { //微信号 6至20位，以字母开头，字母，数字，减号，下划线
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^[A-z]([-_A-z0-9]{5,19})+$/.test(val)
    }
})

Validator.extend('carcard', { //车牌号
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^[京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领A-Z]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9挂学警港澳]{1}$/.test(val)
    }
})

Validator.extend('haschinese', { //包含中文
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /[\u4E00-\u9FA5]/.test(val)
    }
})

Validator.extend('number6', {
    getMessage(fields, args) {
        return ''
    },
    validate(val, args) {
        return /^\d{6}$/.test(val)
    }
})



Vue.use(VeeValidate)



