<template>
  <div>
    <router-link to="/"><button>返回</button></router-link>
    <br>
    昵称:<input
      type="text"
      v-model="nickName"
      placeholder="请输入用户名"
      @blur="singleValidate($event,{nickName})"
    ><br>
    密码:<input
      type="password"
      v-model="password"
      placeholder="请输入密码"
      @blur="singleValidate($event,{password})"
    ><br>
    整数:<input
      type="text"
      v-model="integer"
      placeholder="请输入整数"
      @blur="singleValidate($event,{integer})"
    ><br>
    正整数:<input
      type="text"
      v-model="plusinteger"
      placeholder="请输入正整数"
      @blur="singleValidate($event,{plusinteger})"
    ><br>
    负整数:<input
      type="text"
      v-model="negativeinteger"
      placeholder="请输入负整数"
      @blur="singleValidate($event,{negativeinteger})"
    ><br>
    数字:<input
      type="text"
      v-model="number"
      placeholder="请输入数字"
      @blur="singleValidate($event,{number})"
    ><br>
    正数:<input
      type="text"
      v-model="plusnumber"
      placeholder="请输入正数"
      @blur="singleValidate($event,{plusnumber})"
    ><br>
    负数:<input
      type="text"
      v-model="negativenumber"
      placeholder="请输入正数"
      @blur="singleValidate($event,{negativenumber})"
    ><br>
    邮箱:<input
      type="text"
      v-model="emailLocal"
      placeholder="请输入邮箱"
      @blur="singleValidate($event,{emailLocal})"
    ><br>
    手机号码:<input
      type="text"
      v-model="phone"
      placeholder="请输入手机号码"
      @blur="singleValidate($event,{phone})"
    ><br>
    身份证号码:<input
      type="text"
      v-model="idcard"
      placeholder="请输入身份证号码"
      @blur="singleValidate($event,{idcard})"
    ><br>
    网站地址:<input
      type="text"
      v-model="urlLocal"
      placeholder="请输入网站地址"
      @blur="singleValidate($event,{urlLocal})"
    ><br>
    ipv4:<input
      type="text"
      v-model="ipv4"
      placeholder="请输入ip地址"
      @blur="singleValidate($event,{ipv4})"
    ><br>
    16进制rgb:<input
      type="text"
      v-model="rgb16"
      placeholder="请输入16进制rgb"
      @blur="singleValidate($event,{rgb16})"
    ><br>
    日期简单格式匹配:<input
      type="text"
      v-model="easydate"
      placeholder="请输入日期(如:2000-01-01)"
      @blur="singleValidate($event,{easydate})"
    ><br>
    复杂日期值匹配:<input
      type="text"
      v-model="complexdate"
      placeholder="请输入日期(如:2000-01-01)"
      @blur="singleValidate($event,{complexdate})"
    ><br>
    QQ号码:<input
      type="text"
      v-model="qqcard"
      placeholder="请输入QQ号码"
      @blur="singleValidate($event,{qqcard})"
    ><br>
    微信号码:<input
      type="text"
      v-model="wxcard"
      placeholder="请输入微信号码"
      @blur="singleValidate($event,{wxcard})"
    ><br>
    车牌号:<input
      type="text"
      v-model="carcard"
      placeholder="请输入车牌号码"
      @blur="singleValidate($event,{carcard})"
    ><br>
    中文:<input
      type="text"
      v-model="haschinese"
      placeholder="请输入中文"
      @blur="singleValidate($event,{haschinese})"
    ><br>
    六位整数:<input
      type="text"
      v-model="number6"
      placeholder="请输入6位整数"
      @blur="singleValidate($event,{number6})"
    ><br>
  </div>
</template>


<script>
import VeeValidate from "vee-validate";
/*  校验错误提示s */
const validateErrors = {
  nickName: {
    required: "请输入昵称",
    nickName: "请输入4-14位的昵称(字母，数字，下划线，减号)"
  },
  password: {
    required: "请输入密码",
    password: "密码强度太低(最少6位, 包括一个大写一个小写字母, 一个数字, 一个特殊字符)"
  },
  integer: {
    required: "请输入",
    integer: "请输入整数"
  },
  plusinteger: {
    required: "请输入",
    plusinteger: "请输入正整数"
  },
  negativeinteger: {
    required: "请输入",
    negativeinteger: "请输入负整数"
  },
  number: {
    required: "请输入",
    number: "请输入数字"
  },
  plusnumber: {
    required: "请输入",
    plusnumber: "请输入正数"
  },
  negativenumber: {
    required: "请输入",
    negativenumber: "请输入负数"
  },
  emailLocal: {
    required: "请输入邮箱",
    emailLocal: "邮箱格式不正确"
  },
  phone: {
    required: "请输入手机号码",
    phone: "该手机号码不正确"
  },
  idcard: {
    required: "请输入身份证号码",
    idcard: "该身份证号码不正确"
  },
  urlLocal: {
    required: "请输入网站地址",
    urlLocal: "该网站地址格式不正确"
  },
  ipv4: {
    required: "请输入ip地址",
    ipv4: "该ip地址格式不正确"
  },
  rgb16: {
    required: "请输入16进制rgb",
    rgb16: "该rgb颜色格式不正确"
  },
  easydate: {
    required: "请输入日期",
    easydate: "该日期格式不正确"
  },
  complexdate: {
    required: "请输入日期",
    complexdate: "该日期格式不正确"
  },
  qqcard: {
    required: "请输入QQ号码",
    qqcard: "请输入5-11位的QQ号码"
  },
  wxcard: {
    required: "请输入微信号码",
    wxcard: "请输入6-20位以字母开头的微信号码"
  },
  carcard: {
    required: "请输入车牌号码",
    carcard: "车牌号码格式不正确"
  },
  haschinese: {
    required: "请输入中文",
    haschinese: "必须含有中文"
  },
  number6: {
    required: "请输入六位整数",
    number6: "必须时六位整数"
  },
};
export default {
  data() {
    return {
      nickName: "",
      password: "",
      integer: "",
      plusinteger: "",
      negativeinteger: "",
      number: "",
      plusnumber: "",
      negativenumber: "",
      emailLocal: "",
      phone: "",
      idcard: "",
      urlLocal: "",
      ipv4: "",
      rgb16: "",
      easydate: "",
      complexdate: "",
      qqcard: "",
      wxcard: "",
      carcard: "",
      haschinese: "",
      number6: "",
    };
  },
  created() {
    this.validator = new VeeValidate.Validator({
      nickName: "required|nickName",
      password: "required|password",
      integer: "required|integer",
      plusinteger: "required|plusinteger",
      negativeinteger: "required|negativeinteger",
      number: "required|number",
      plusnumber: "required|plusnumber",
      negativenumber: "required|negativenumber",
      emailLocal: "required|emailLocal",
      phone: "required|phone",
      idcard: "required|idcard",
      urlLocal: "required|urlLocal",
      ipv4: "required|ipv4",
      rgb16: "required|rgb16",
      easydate: "required|easydate",
      complexdate: "required|complexdate",
      qqcard: "required|qqcard",
      wxcard: "required|wxcard",
      carcard: "required|carcard",
      haschinese: "required|haschinese",
      number6: "required|number6",
    });
  },
  methods: {
    singleValidate(e, data, cb, failCb) {
      let validateInfo = {};
      Object.keys(data).forEach(v => {
        validateInfo[v] = data[v];
      });
      this.validator.errorBag.errors.length = 0;
      this.validator.validateAll(validateInfo).then(
        res => {
          // this.$toast(`验证成功`);
          cb && cb();
        },
        err => {
          this.$toast(
            validateErrors[this.validator.errorBag.errors[0].field][
              this.validator.errorBag.errors[0].rule
            ]
          );
          failCb && failCb();
        }
      );
    }
  }
};
</script>

<style lang="less" scoped>
</style>




