import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Validate1 from '@/views/validateTestDemo/Demo' //import引入
const Validate2 = r => require.ensure([], () => r(require('@/views/validateTestDemo/Demo')), 'validateTestDemo') // require.ensure引入
const Validate3 = resolve => require(['@/views/validateTestDemo/Demo'], resolve) //amd加载

Vue.use(Router)

/* 首屏加载用import 跳转型页面用amd */



export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    { // 正常加载
      path: '/validate',
      name: 'Validate',
      component: Validate2
    },
    // { /* 懒加载 */
    //   path: '/validate',
    //   name: 'Validate',
    //   component: resolve => {
    //     require(['@/views/validateTestDemo/Demo'], resolve)
    //   }
    // },
  ]
})



