<template>
  <div class="hello">
    <router-link to="validate">
      <h1>{{ msg }}</h1>
    </router-link>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "表单验证示例",
      info: ""
    };
  },
  mounted() {
    this.ajaxFn();
  },
  methods: {
    ajax1(item) {
      return new Promise((resolve, reject) => {
        this.$http({
          url: `http://suggest.taobao.com/sug?code=utf-8&q=${item}&callback=cb://www.baidu.com`
        }).then(res => {
          resolve(res);
        });
      });
    },
    ajax2(item) {
      return new Promise((resolve, reject) => {
        this.$http({
          url: `http://suggest.taobao.com/sug?code=utf-8&q=${item}&callback=cb://www.baidu.com`
        }).then(res => {
          resolve(res);
        });
      });
    },
    async ajaxFn() {
      //async 异步的同步写法
      let res1 = await this.ajax1("水果"); //第一个ajax请求
      console.log(res1);
      let res2 = await this.ajax2("电脑"); //第二个ajax请求
      console.log(res2);
    },
    gotoValidate() {
      this.$router();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.fa {
  color: red;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 0.1rem;
}
a {
  color: #42b983;
}
</style>



