export default (() => {
    class RemFn {
        constructor({ mw, design, base }) {
            this.mw = mw
            this.design = design
            this.base = base
        }
        resizeUpdate() {
            let currentWidth = document.documentElement.clientWidth
            currentWidth < this.mw && (currentWidth = this.mw)
            document.documentElement.style.fontSize = `${currentWidth / this.design * this.base}px`
        }
        getRem() {
            let timer = null
            this.resizeUpdate()
            
            window.addEventListener('resize', () => {
                clearTimeout(timer)
                timer = setTimeout(() => { this.resizeUpdate() }, 10)
            }, false)
        }

    }

    const options = {
        mw: 375,
        design: 375,
        base: 100
    }
    const rem = new RemFn({ ...options })

    rem.getRem()

})()




